.register_form {
    width: min(70%, 400px);
    background: white;
    margin: 0 auto;
    height: fit-content;
    padding: 40px 0 30px 0;
    border: 2px solid #eeeeee;
}

.register_form form {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
    background-color: white;
}

.register_form h1 {
    background-color: #86c39a;
    color: white;
    letter-spacing: 0.05em;
    border-radius: 20px;
    padding: 0.3em;
    width: fit-content;
    margin: 0 auto 20px auto;
    /* box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 4px 0px inset; */
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
        rgba(0, 0, 0, 0.3) 0px 7px 13px -3px,
        rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.register_form input {
    padding: 8px;
    border-radius: 10px;
    border: 1px solid #cdcbcb;
}

.register_form input:focus {
    padding: 8px;
    border-radius: 10px;
    border-bottom: 2px solid #8db286;
    outline: none;
}

.register_msg {
    font-size: 0.8rem;
    color: black;
    padding: 10px;
}
